/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

@font-face {
  font-family: 'SWFont';
  src: url("../SerifGothicBQ-Heavy.otf") format("truetype"); }

html, body {
  height: 100%;
  min-height: 800px;
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-size: 1.6rem;
  font-family: 'SWFont', sans-serif;
  background-image: url("../img/space-pattern.jpg");
  background-repeat: repeat;
  background-color: black; }

.no-select {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }

a {
  text-decoration: none;
  color: currentColor; }

.main-section {
  position: relative;
  width: 100%;
  height: 100%; }

.video {
  width: 100%;
  height: 100%;
  overflow: hidden;
  border-bottom: 2px solid #E6B229; }

video {
  width: 100%;
  height: 100%;
  object-fit: cover; }

.container {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  z-index: 999; }

.logo {
  height: 200px;
  margin: 15px; }

.clock {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap; }

.clock-piece {
  width: 200px;
  margin: 15px;
  display: flex;
  flex-direction: column; }

.value {
  width: 100%;
  font-size: 10rem;
  text-align: center;
  color: #fff;
  text-shadow: 1px 0px 2px rgba(150, 150, 150, 0.7); }

.label {
  width: 100%;
  font-size: 3rem;
  text-align: center;
  color: #E6B229;
  text-transform: uppercase;
  text-shadow: 1px 0px 2px rgba(150, 150, 150, 0.7); }

.video-footer {
  position: relative;
  height: 200px;
  color: #fff;
  margin: 30px 0 15px 0;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  font-family: Verdana, sans-serif;
  font-size: 0.8rem;
  text-align: center; }
  .video-footer .social {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 15px; }
    .video-footer .social .twitter-share-button {
      margin-right: 10px; }

.content-section {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 20px;
  text-align: center; }

h1 {
  font-size: 6rem;
  color: #fff;
  padding: 60px; }

h2 {
  font-size: 4rem;
  color: #E6B229;
  padding-left: 60px;
  padding-right: 60px;
  padding-bottom: 30px; }

.videoWrapper {
  position: relative;
  padding-bottom: 56.25%;
  /* 16:9 */
  padding-top: 25px;
  height: 0; }

.videoWrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

footer {
  position: absolute;
  bottom: 0;
  color: #fff;
  margin: 30px 0 15px 0;
  font-family: Verdana, sans-serif;
  font-size: 0.8rem;
  text-align: center; }

@media only screen and (max-width: 1300px) {
  .logo {
    height: 150px; } }

@media only screen and (max-width: 1000px) {
  .logo {
    height: 100px; }
  .clock {
    flex-direction: column; }
  .value {
    font-size: 7rem; }
  .label {
    font-size: 2rem; }
  .video {
    border-bottom: 2px solid transparent; }
  video {
    display: none; }
  .video-footer {
    height: 100px; }
  h1 {
    font-size: 4rem; }
  h2 {
    font-size: 2rem; } }
